.header.shadow {
  -webkit-box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
  -moz-box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
  box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3); }

.icon {
  font-family: iconfont; }

@font-face {
  font-family: 'iconfont';
  /* project id 1333053 */
  src: url("//at.alicdn.com/t/font_1333053_2yq9g98dflj.eot");
  src: url("//at.alicdn.com/t/font_1333053_2yq9g98dflj.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_1333053_2yq9g98dflj.woff2") format("woff2"), url("//at.alicdn.com/t/font_1333053_2yq9g98dflj.woff") format("woff"), url("//at.alicdn.com/t/font_1333053_2yq9g98dflj.ttf") format("truetype"), url("//at.alicdn.com/t/font_1333053_2yq9g98dflj.svg#iconfont") format("svg"); }

html, body {
  overflow-x: hidden;
  overflow-y: auto; }

* {
  -webkit-tap-highlight-color: transparent; }

ul, ol {
  margin: 0;
  padding: 0; }

input {
  outline: none; }

#root {
  position: relative;
  height: 100vh; }

.fill-parent {
  min-height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column; }

.fill-space {
  flex: 1; }

.absolute-parent {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }

.page-animation {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

.subpages {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

.subpages-empty {
  pointer-events: none; }

.clearfix:after {
  content: "\200B";
  display: block;
  height: 0;
  clear: both; }

html {
  height: 100%; }

body {
  height: 100%;
  margin: 0;
  font-family: "Microsoft Yahei", sans-serif;
  font-weight: normal; }

div {
  z-index: 0;
  position: relative;
  margin: 0;
  box-sizing: border-box; }

p {
  margin: 0;
  font-size: 16px; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 500; }
